/**
 * A file that contains most of the Vue Components and logic
 */
import axios from "axios";
import Vue from "vue";
import { store } from "./Vuex/store.js";
import { MediaQueries } from "vue-media-queries";
import VueAccessibleModal from "vue-accessible-modal";
window.Vue = Vue;
var mediaQueries = new MediaQueries();
Vue.use(mediaQueries);
/** Vue Accessibility Default Options **/

var modalOptions = {
  transition: 'fade'
};
Vue.use(VueAccessibleModal, modalOptions);
Vue.config.productionTip = false;
var navigationElm = document.getElementById("vue-navigation-container");
var VueInstance = new Vue({
  store: store,
  mediaQueries: mediaQueries,
  created: function created() {
    if (typeof NAV !== "undefined" && navigationElm !== null) {
      this.$store.dispatch('getNavigation', {
        viewport: "desktop",
        navID: NAV.navID
      });
      this.$store.dispatch('getNavigation', {
        viewport: "mobile",
        navID: NAV.navID
      });
    }
  }
});
/**
 * The Navigation
 */

if (navigationElm !== null) {
  var navigationContainer = Vue.component("NavigationContainer", require('./Components/Navigation.vue').default);

  var _navigationElm = new Vue({
    el: "#vue-navigation-container",
    mediaQueries: mediaQueries,
    store: store,
    render: function render(h) {
      return h(navigationContainer);
    }
  });
}

if (document.getElementById("modal-cnt") !== null) {
  var ModalWindow = Vue.component("modal", require('./Components/ModalContainer.vue').default);
  var modalElm = new Vue({
    el: "#modal-cnt",
    render: function render(h) {
      return h(ModalWindow);
    }
  });
}


