import Modal from "./Modal/Modal.vue";
export default {
  data: function data() {
    return {
      show: false
    };
  },
  methods: {
    showModal: function showModal() {
      this.$modal.show(Modal, {
        label: 'Accessible Modal',
        attributes: {
          id: 'accessible-modal'
        }
      });
    }
  },
  name: "ModalContainer"
};


